<template>
  <div class="max-container">
    <el-row :gutter="12">
      <el-col :span="6">
        <el-row>
          <el-card class="task-settings">
            <el-row>
              <div style="text-align: left;">
                <el-tag><a class="task-tag">任务设置</a></el-tag>
              </div>
            </el-row>
            <el-row>
              <el-form ref="taskSettings" :model="taskSettings" label-width="80px">
                <el-form-item label="损失函数" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="12">
                    <el-select v-model="taskSettings.lossFunction" :disabled="taskSettingsDisabled">
                      <el-option label="Sphere" value="Sphere"/>
                      <el-option label="Easom" value="Easom"/>
                      <el-option label="Giewangk" value="Giewangk"/>
                    </el-select>
                  </el-col>
                </el-form-item>
                <el-form-item label="搜索范围" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="8">
                    <el-input v-model="taskSettings.rangeMin" :disabled="taskSettingsDisabled" clearable/>
                  </el-col>
                  <el-col :span="2"> ~ </el-col>
                  <el-col :span="8">
                    <el-input v-model="taskSettings.rangeMax" :disabled="taskSettingsDisabled" clearable/>
                  </el-col>
                </el-form-item>
                <el-form-item label="优化算法" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="12">
                    <el-select v-model="taskSettings.algorithm" :disabled="taskSettingsDisabled">
                      <el-option label="ACO" value="ACO"/>
                    </el-select>
                  </el-col>
                </el-form-item>
                <el-form-item style="margin-bottom: 10px; padding: 0px 10px; margin-right: 80px">
                  <el-button type="primary" style="padding: 5px 5px" icon="el-icon-edit" @click="editTaskSettings" :disabled="settingsDisabled"/>
                  <el-button type="success" style="padding: 5px 5px" icon="el-icon-check" @click="saveTaskSettings" :disabled="settingsDisabled"/>
                </el-form-item>
              </el-form>
            </el-row>
          </el-card>
        </el-row>
        <el-row>
          <el-card class="parameter-settings">
            <el-row>
              <div style="text-align: left;">
                <el-tag><a class="parameter-tag">参数配置</a></el-tag>
              </div>
            </el-row>
            <el-row>
              <el-form v-if="taskSettings.algorithm=='ACO'" ref="ACOSettings" :model="ACOSettings" label-width="80px">
                <el-form-item label="优胜名额" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="12">
                    <el-select v-model="ACOSettings.winnerCount" :disabled="parameterSettingsDisabled">
                      <el-option label="5" value="5"/>
                      <el-option label="10" value="10"/>
                      <el-option label="20" value="20"/>
                      <el-option label="40" value="40"/>
                    </el-select>
                  </el-col>
                </el-form-item>
                <el-form-item label="扩增名额" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="12">
                    <el-select v-model="ACOSettings.loserCount" :disabled="parameterSettingsDisabled">
                      <el-option label="20" value="20"/>
                      <el-option label="40" value="40"/>
                      <el-option label="60" value="60"/>
                      <el-option label="80" value="80"/>
                    </el-select>
                  </el-col>
                </el-form-item>
                <el-form-item label="权重因子" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="8">
                    <el-input v-model="ACOSettings.weightFactor" :disabled="parameterSettingsDisabled" clearable/>
                  </el-col>
                </el-form-item>
                <el-form-item label="进化因子" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="8">
                    <el-input v-model="ACOSettings.evolutionMultiplier" :disabled="parameterSettingsDisabled" clearable/>
                  </el-col>
                </el-form-item>
                <el-form-item label="进化指数" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="8">
                    <el-input v-model="ACOSettings.evolutionExponent" :disabled="parameterSettingsDisabled" clearable/>
                  </el-col>
                </el-form-item>
                <el-form-item label="迭代周期" style="margin-top: 10px; margin-bottom: 10px; margin-left: 20px">
                  <el-col :span="12">
                    <el-select v-model="ACOSettings.maxIteration" :disabled="parameterSettingsDisabled">
                      <el-option label="10" value="10"/>
                      <el-option label="20" value="20"/>
                      <el-option label="50" value="50"/>
                      <el-option label="100" value="100"/>
                      <el-option label="200" value="200"/>
                    </el-select>
                  </el-col>
                </el-form-item>
                <el-form-item style="margin-bottom: 10px; padding: 0px 10px; margin-right: 80px">
                  <el-button type="primary" style="padding: 5px 5px" icon="el-icon-edit" @click="editParameterSettings" :disabled="settingsDisabled"/>
                  <el-button type="success" style="padding: 5px 5px" icon="el-icon-check" @click="saveParameterSettings" :disabled="settingsDisabled"/>
                </el-form-item>
              </el-form>
              <el-form v-else-if="taskSettings.algorithm='DACO'">
              </el-form>
              <el-form v-else>
              </el-form>
            </el-row>
            <el-row>
              <div class="boat"/>
            </el-row>
          </el-card>
        </el-row>
      </el-col>
      <el-col :span="18">
        <el-card class="vision-container">
          <el-col :span="2">
            <div style="width: 60px; padding-left: 0px; padding-right: 0px;">
              <el-button style="height: 60px; width: 60px; padding: 0px; margin-left: 0px;" @click="checkSimulation" v-loading.fullscreen.lock="isSimulation">
                <img src="../assets/simulation.png" style="height: 50px; width: 50px; margin: 0px"/>
              </el-button>
              <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 20px; margin-left: 0px;" @click="checkStepRunning">
                <img src="../assets/debug.png" style="height: 45px; width: 45px; margin: 0px"/>
              </el-button>
              <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 20px; margin-left: 0px;" @click="outputLog">
                <img src="../assets/text.png" style="height: 50px; width: 50px; margin: 0px"/>
              </el-button>
              <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 20px; margin-left: 0px;" @click="outputLoss">
                <img src="../assets/descent.png" style="height: 50px; width: 50px; margin: 0px"/>
              </el-button>
              <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 20px; margin-left: 0px;" @click="output2D">
                <img src="../assets/xy.png" style="height: 50px; width: 50px; margin: 0px"/>
              </el-button>
              <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 20px; margin-left: 0px;" @click="output3D">
                <img src="../assets/xyz.png" style="height: 50px; width: 50px; margin: 0px"/>
              </el-button>
              <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 20px; margin-left: 0px;" @click="quit">
                <img src="../assets/quit.png" style="height: 50px; width: 50px; margin: 0px"/>
              </el-button>
            </div>
          </el-col>
          <el-col :span="22" style="height: 700px;">
            <div class='output-panel' v-show="showBackground"/>
            <el-input v-show="showLog" id="log" type="textarea" :rows="30" v-model="simulationLog" readonly/>
            <div v-show="showLoss" id="loss" class="loss-chart"/>
            <div v-show="show2D" id="2D" class="xy-chart"/>
            <div v-show="show3D" id="3D" class="xyz-chart"/>
            <div v-show="showStepRunning">
              <el-row>
                <el-col :span="18">
                  <div id="trace" class="trace-chart"/>
                </el-col>
                <el-col :span="6">
                  <div class="trace-controller" style="text-align: center; vertical-align: middle">
                    <el-row style="height: 160px">
                      <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 100px; margin-right: 20px;" @click="undo" :disabled="undoDisabled">
                        <img src="../assets/undo.png" style="height: 45px; width: 45px; margin: 0px"/>
                      </el-button>
                      <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 100px; margin-left: 20px;" @click="redo" :disabled="redoDisabled">
                        <img src="../assets/todo.png" style="height: 45px; width: 45px; margin: 0px"/>
                      </el-button>
                    </el-row>
                    <el-row style="height: 160px">
                      <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 50px; margin-right: 20px;" @click="add" :disabled="addDisabled">
                        <img src="../assets/add.png" style="height: 45px; width: 45px; margin: 0px"/>
                      </el-button>
                      <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 50px; margin-left: 20px;" @click="remove" :disabled="removeDisabled">
                        <img src="../assets/remove.png" style="height: 45px; width: 45px; margin: 0px"/>
                      </el-button>
                    </el-row>
                    <el-row style="height: 160px">
                      <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 0px; margin-right: 20px;" @click="zoomIn">
                        <img src="../assets/zoom-in.png" style="height: 45px; width: 45px; margin: 0px"/>
                      </el-button>
                      <el-button style="height: 60px; width: 60px; padding: 0px; margin-top: 0px; margin-left: 20px;" @click="zoomOut">
                        <img src="../assets/zoom-out.png" style="height: 45px; width: 45px; margin: 0px"/>
                      </el-button>
                    </el-row>
                  </div>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="18">
                  <div class="trace-log">
                    <el-input type="textarea" :rows="10" v-model="stepRunningLog" readonly/>
                  </div>
                </el-col>
                <el-col :span="6">
                  <div class="trace-wheel">
                    <div class="wheel-pointer" @click="roll"/>
                    <div class="wheel-bg"  :class="{freeze: freeze}" :style="`transform: rotate(${wheelDeg}deg)`">
                      <div class="prize-item-wrapper" v-for="(item, index) in wheelSelection" :key="index">
                        <div class="prize-item" :style="`transform: rotate(${360/wheelSelection.length*index}deg)`">
                          <div class="prize-name">
                            {{ index }}
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </el-col>
              </el-row>
            </div>
          </el-col>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>

export default {
  data() {
    return {
      mode: 'setting',
      outputMode: '',
      simulationLog: '',
      stepRunningLog: '',

      taskSettingsDisabled: false,
      taskSettings: {
        lossFunction: 'Sphere',
        rangeMin: -10,
        rangeMax: 10,
        algorithm: 'ACO'
      },
      parameterSettingsDisabled: false,
      ACOSettings: {
        winnerCount: 10,
        loserCount: 40,
        weightFactor: 0.5,
        evolutionMultiplier: 1,
        evolutionExponent: 0.5,
        maxIteration: 50
      },

      loss: null,
      wheelSelection: [],
      iterationCount: 0,
      nodeList: [],
      nodeArray: [],
      selectNode: null,
      chart: null,
      option: null,

      freeze: false,
      rolling: false,
      wheelDeg: 0,
      activeStepIndex: 0,
      stepOperationCount: 0
    }
  },
  computed: {
    settingsDisabled: function() {
      return this.mode != 'setting' && this.mode != 'ready';
    },
    showBackground: function() {
      return this.mode == 'setting' || this.mode == 'ready';
    },
    isSimulation: function() {
      return this.mode == 'simulation';
    },
    showLog: function() {
      return this.mode == 'output' && this.outputMode == 'log';
    },
    showLoss: function() {
      return this.mode == 'output' && this.outputMode == 'loss';
    },
    show2D: function() {
      return this.mode == 'output' && this.outputMode == '2D';
    },
    show3D: function() {
      return this.mode == 'output' && this.outputMode == '3D';
    },
    showStepRunning: function() {
      return this.mode == 'stepRunning';
    },
    undoDisabled: function() {
      return this.activeStepIndex == 0;
    },
    redoDisabled: function() {
      if (this.nodeArray == null) {
        return true;
      }
      return this.activeStepIndex == this.nodeArray.length - 1;
    },
    rollDisabled: function() {
      return (this.activeStepIndex != this.nodeArray.length - 1) || (this.stepOperationCount % 3 != 0);
    },
    addDisabled: function() {
      if (this.nodeArray == null) {
        return true;
      }
      return (this.activeStepIndex != this.nodeArray.length - 1) && (this.stepOperationCount % 3 != 1);
    },
    removeDisabled: function() {
      if (this.nodeArray == null) {
        return true;
      }
      return (this.activeStepIndex != this.nodeArray.length - 1) && (this.stepOperationCount % 3 != 2);
    }
  },
  methods: {
    editTaskSettings() {
      this.taskSettingsDisabled = false;
      this.mode = 'setting';
    },
    saveTaskSettings() {
      this.taskSettingsDisabled = true;
      this.$message({
        type: 'success',
        message: '任务已设置'
      });
      if (this.parameterSettingsDisabled) {
        this.mode = 'ready';
        this.simulationLog = '';
        this.nodeArray = null;
        this.$message({
          type: 'success',
          message: '配置完成，数据已重置，可开始仿真（调试）'
        });
      }
    },
    editParameterSettings(){
      this.parameterSettingsDisabled = false;
      this.mode = 'setting';
    },
    saveParameterSettings(){
      this.parameterSettingsDisabled = true;
      this.$message({
        type: 'success',
        message: '参数已设置'
      })
      if (this.taskSettingsDisabled) {
        this.mode = 'ready';
        this.simulationLog = '';
        this.nodeArray = null;
        this.$message({
          type: 'success',
          message: '配置完成，数据已重置，可开始仿真（调试）'
        });
      }
    },
    checkSimulation() {
      if (this.mode == 'setting') {
        this.$message.error('请先保存参数再启动仿真');
      } else if (this.mode != 'ready') {
        this.$message.error('请先退出其它功能界面再启动仿真');
      } else {
        this.$confirm('即将启动仿真，是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.mode = 'simulation';
          this.simulation();
          setTimeout(() => {
            this.mode = 'ready';
          }, 1000);
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消仿真'
          })
        });
      }
    },
    simulation() {
      this.simulationLog = '启发式搜索算法仿真启动(损失函数：Sphere，搜索范围：[' + this.taskSettings.rangeMin + ',' + this.taskSettings.rangeMax + ']，优化算法：ACO)\n';
      
      this.simulationLog += '初始化轮盘赌向量：';
      this.wheelSelection = [];
      var sigma = this.ACOSettings.weightFactor * this.ACOSettings.winnerCount;
      var sum = 0;
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        var weight = Math.exp(-0.5 * i * i / (sigma * sigma)) / (Math.sqrt(2 * Math.PI) * sigma);
        this.wheelSelection.push(weight);
        sum += weight;
      }
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        this.wheelSelection[i] /= sum;
      }
      for (let i = 1; i < this.ACOSettings.winnerCount; i++) {
        this.wheelSelection[i] += this.wheelSelection[i - 1];
      }
      this.simulationLog += '[' + this.wheelSelection[0].toFixed(3);
      for (let i = 1; i < this.ACOSettings.winnerCount; i++) {
        this.simulationLog += ', ' + this.wheelSelection[i].toFixed(3); 
      }
      this.simulationLog += ']\n';

      this.simulationLog += '初始化节点坐标：\n';
      this.nodeList = [];
      if (this.taskSettings.lossFunction == 'Sphere') {
        this.loss = this.sphere;
      } else if (this.taskSettings.lossFunction == 'Giewangk') {
        this.loss = this.giewangk;
      } else {
        this.loss = this.easom;
      }
      this.taskSettings.rangeMin = parseFloat(this.taskSettings.rangeMin);
      this.taskSettings.rangeMax = parseFloat(this.taskSettings.rangeMax);
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        let x = Math.random() * (this.taskSettings.rangeMax - this.taskSettings.rangeMin) + this.taskSettings.rangeMin;
        let y = Math.random() * (this.taskSettings.rangeMax - this.taskSettings.rangeMin) + this.taskSettings.rangeMin;
        this.nodeList.push([x, y, this.loss(x, y)]);
      }
      this.nodeList.sort(function(node1, node2) {
        return node1[2] - node2[2];
      });
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        this.simulationLog += '(' + this.nodeList[i][0] + ', ' + this.nodeList[i][1] + ')';
        if (i % 2 == 1) {
          this.simulationLog += '\n';
        } else {
          this.simulationLog += ' ';
        }
      }
      if (this.ACOSettings.winnerCount % 2 != 0) {
        this.simulationLog += '\n';
      }
      
      this.nodeArray = [];
      this.iterationCount = 0;
      while (this.iterationCount < this.ACOSettings.maxIteration) {
        this.simulationLog += '第 ' + this.iterationCount + ' 代抽选: ';
        var randomSeed = Math.random();
        for (let j = 0; j < this.wheelSelection.length; j++) {
          if (randomSeed < this.wheelSelection[j]) {
            this.simulationLog += '(' + this.nodeList[j][0] + ', ' + this.nodeList[j][1] + ') ';
            this.selectNode = this.nodeList[j];
            break;
          }
        }

        this.simulationLog += '进化标准差：';
        var xDev = 0, yDev = 0;
        for (let j = 0; j < this.ACOSettings.winnerCount; j++) {
          xDev += Math.abs(this.nodeList[j][0] - this.selectNode[0]);
          yDev += Math.abs(this.nodeList[j][1] - this.selectNode[1]);
        }
        xDev = this.ACOSettings.evolutionMultiplier * Math.pow(xDev, this.ACOSettings.evolutionExponent) / (this.ACOSettings.winnerCount - 1);
        yDev = this.ACOSettings.evolutionMultiplier * Math.pow(yDev, this.ACOSettings.evolutionExponent) / (this.ACOSettings.winnerCount - 1);
        this.simulationLog += '(' + xDev + ', ' + yDev + ')\n';

        this.simulationLog += '第 ' + this.iterationCount + ' 代进化：\n';
        var sampleCount = 0;
        while (sampleCount < this.ACOSettings.loserCount) {
          var sampleX = this.selectNode[0] + this.normal() * xDev;
          if (sampleX < this.taskSettings.rangeMin || sampleX > this.taskSettings.rangeMax) {
            continue;
          }
          var sampleY = this.selectNode[1] + this.normal() * yDev;
          if (sampleY < this.taskSettings.rangeMin || sampleY > this.taskSettings.rangeMax) {
            continue;
          }

          this.nodeList.push([sampleX, sampleY, this.loss(sampleX, sampleY)]);
          this.simulationLog += '(' + sampleX + ', ' + sampleY + ')';
          if (sampleCount % 2 == 1) {
            this.simulationLog += '\n';
          } else {
            this.simulationLog += ' ';
          }
          sampleCount += 1;
        }
        if (this.ACOSettings.loserCount % 2 != 0) {
          this.simulationLog += '\n';
        }

        this.simulationLog += '第 ' + this.iterationCount + ' 代优胜：\n';
        this.nodeList.sort(function(node1, node2) {
          return node1[2] - node2[2];
        });
        for (let j = 0; j < this.ACOSettings.winnerCount; j++) {
          this.simulationLog += '(' + this.nodeList[j][0] + ', ' + this.nodeList[j][1] + ')';
          if (j % 2 == 1) {
            this.simulationLog += '\n';
          } else {
            this.simulationLog += ' ';
          }
        }
        if (this.ACOSettings.winnerCount % 2 != 0) {
          this.simulationLog += '\n';
        }
        this.nodeArray.push(this.nodeList);

        if (this.iterationCount != this.ACOSettings.maxIteration - 1) {
          this.nodeList = [];
          for (let j = 0; j < this.ACOSettings.winnerCount; j++) {
            this.nodeList.push(this.nodeArray[this.iterationCount][j]);
          }
        }
        this.iterationCount += 1;
      }
    },
    checkStepRunning() {
      if (this.mode == 'setting') {
        this.$message.error('请先保存参数再启动单步调试');
      } else if (this.mode != 'ready') {
        this.$message.error('请先退出其它功能界面再启动单步调试');
      } else {
        this.$confirm('即将启动单步调试，是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.initStepRunning();
          this.mode = 'stepRunning';
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消单步调试'
          })
        });
      }
    },
    initStepRunning() {
      this.stepRunningLog += '初始化轮盘赌向量：';
      this.wheelSelection = [];
      var sigma = this.ACOSettings.weightFactor * this.ACOSettings.winnerCount;
      var sum = 0;
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        var weight = Math.exp(-0.5 * i * i / (sigma * sigma)) / (Math.sqrt(2 * Math.PI) * sigma);
        this.wheelSelection.push(weight);
        sum += weight;
      }
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        this.wheelSelection[i] /= sum;
      }
      for (let i = 1; i < this.ACOSettings.winnerCount; i++) {
        this.wheelSelection[i] += this.wheelSelection[i - 1];
      }
      this.stepRunningLog += '[' + this.wheelSelection[0].toFixed(3);
      for (let i = 1; i < this.ACOSettings.winnerCount; i++) {
        this.stepRunningLog += ', ' + this.wheelSelection[i].toFixed(3); 
      }
      this.stepRunningLog += ']\n';

      this.stepRunningLog += '初始化节点坐标：\n';
      this.nodeList = [];
      if (this.taskSettings.lossFunction == 'Sphere') {
        this.loss = this.sphere;
      } else if (this.taskSettings.lossFunction == 'Giewangk') {
        this.loss = this.giewangk;
      } else {
        this.loss = this.easom;
      }
      this.taskSettings.rangeMin = parseFloat(this.taskSettings.rangeMin);
      this.taskSettings.rangeMax = parseFloat(this.taskSettings.rangeMax);
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        let x = Math.random() * (this.taskSettings.rangeMax - this.taskSettings.rangeMin) + this.taskSettings.rangeMin;
        let y = Math.random() * (this.taskSettings.rangeMax - this.taskSettings.rangeMin) + this.taskSettings.rangeMin;
        this.nodeList.push([x, y, this.loss(x, y)]);
      }
      this.nodeList.sort(function(node1, node2) {
        return node1[2] - node2[2];
      });

      this.nodeArray = [];
      this.nodeArray.push(this.nodeList);
      for (let i = 0; i < this.ACOSettings.winnerCount; i++) {
        this.stepRunningLog += '(' + this.nodeList[i][0] + ', ' + this.nodeList[i][1] + ')';
        if (i % 2 == 1) {
          this.stepRunningLog += '\n';
        } else {
          this.stepRunningLog += ' ';
        }
      }
      if (this.ACOSettings.winnerCount % 2 != 0) {
        this.stepRunningLog += '\n';
      }

      var sampleRate = 200;
      var lossMin = Infinity, lossMax = -Infinity;
      for (let i = 0; i <= sampleRate; i++) {
        let x = this.taskSettings.rangeMin + (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate * i;
        for (let j = 0; j <= sampleRate; j++) {
          let y = this.taskSettings.rangeMin + (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate * j;
          let value = this.loss(x, y);
          if (value < lossMin) {
            lossMin = value;
          }
          if (value > lossMax) {
            lossMax = value;
          }
        }
      }

      this.chart = this.$echarts.init(document.getElementById('trace'));
      this.option = {
        tooltip: {},
        visualMap: {
          show: true,
          dimension: 2,
          min: lossMin,
          max: lossMax,
          inRange: {
              color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
          },
          left: 30,
          bottom: 20,
          seriesIndex: 0
        },
        xAxis3D: {
            type: 'value'
        },
        yAxis3D: {
            type: 'value'
        },
        zAxis3D: {
            type: 'value'
        },
        grid3D: {
            viewControl: {
              projection: 'orthographic'
            },
            boxHeight: 60,
            left: 30
        },
        series: [
          {
            type: 'surface',
            wireframe: {
              show: false
            },
            shading: 'color',
            equation: {
              x: {
                step: (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate,
                min: this.taskSettings.rangeMin,
                max: this.taskSettings.rangeMax
              },
              y: {
                step: (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate,
                min: this.taskSettings.rangeMin,
                max: this.taskSettings.rangeMax
              },
              z: this.loss
            },
            itemStyle: {
              opacity: 0.8
            }
          },
          {
            type: 'line3D',
            data: [],
            lineStyle: {
              width: 4,
              color: 'black'
            }
          },
          {
            type: 'scatter3D',
            data: this.nodeList,
            symbol: 'triangle',
            symbolSize: 10,
            itemStyle: {
              color: 'red'
            }
          },
          {
            type: 'scatter3D',
            data: [],
            symbol: 'triangle',
            symbolSize: 10,
            itemStyle: {
              color: 'blue'
            }
          }
        ]
      };
      this.chart.setOption(this.option);    
      this.stepRunningLog += '初始化完成，可以开始单步调试！\n';
    },
    drawTrace() {
      var sampleRate = 200;
      var lossMin = Infinity, lossMax = -Infinity;
      for (let i = 0; i <= sampleRate; i++) {
        let x = this.taskSettings.rangeMin + (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate * i;
        for (let j = 0; j <= sampleRate; j++) {
          let y = this.taskSettings.rangeMin + (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate * j;
          let value = this.loss(x, y);
          if (value < lossMin) {
            lossMin = value;
          }
          if (value > lossMax) {
            lossMax = value;
          }
        }
      }

      this.option.visualMap.min = lossMin;
      this.option.visualMap.max = lossMax;
      this.option.series[0].equation.x = {
        step: (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate,
        min: this.taskSettings.rangeMin,
        max: this.taskSettings.rangeMax
      }
      this.option.series[0].equation.y = {
        step: (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate,
        min: this.taskSettings.rangeMin,
        max: this.taskSettings.rangeMax        
      }

      var trace = [];
      for (let i = 0; i < this.activeStepIndex; i++) {
        trace.push(this.nodeArray[i][0]);
      }
      this.option.series[1].data = trace;
      this.option.series[2].data = this.nodeArray[this.activeStepIndex];
      this.option.series[3].data = [];
      this.chart.setOption(this.option);
    },
    undo() {
      this.activeStepIndex--;
      this.drawTrace();
    },
    redo() {
      this.activeStepIndex++;
      this.drawTrace();
    },
    add() {
      this.stepRunningLog += '进化标准差：';
      var xDev = 0, yDev = 0;
      for (let j = 0; j < this.nodeList.length; j++) {
        xDev += Math.abs(this.nodeList[j][0] - this.selectNode[0]);
        yDev += Math.abs(this.nodeList[j][1] - this.selectNode[1]);
      }
      xDev = this.ACOSettings.evolutionMultiplier * Math.pow(xDev, this.ACOSettings.evolutionExponent) / (this.ACOSettings.winnerCount - 1);
      yDev = this.ACOSettings.evolutionMultiplier * Math.pow(yDev, this.ACOSettings.evolutionExponent) / (this.ACOSettings.winnerCount - 1);
      this.stepRunningLog += '(' + xDev + ', ' + yDev + ')\n';

      this.stepRunningLog += '第 ' + this.activeStepIndex + ' 代进化：\n';
      var sampleCount = 0;
      while (sampleCount < this.ACOSettings.loserCount) {
        var sampleX = this.selectNode[0] + this.normal() * xDev;
        if (sampleX < this.taskSettings.rangeMin || sampleX > this.taskSettings.rangeMax) {
          continue;
        }
        var sampleY = this.selectNode[1] + this.normal() * yDev;
        if (sampleY < this.taskSettings.rangeMin || sampleY > this.taskSettings.rangeMax) {
          continue;
        }

        this.nodeList.push([sampleX, sampleY, this.loss(sampleX, sampleY)]);
        this.stepRunningLog += '(' + sampleX + ', ' + sampleY + ')';
        if (sampleCount % 2 == 1) {
          this.stepRunningLog += '\n';
        } else {
          this.stepRunningLog += ' ';
        }
        sampleCount += 1;
      }
      if (this.ACOSettings.loserCount % 2 != 0) {
        this.stepRunningLog += '\n';
      }

      for (let i = this.ACOSettings.winnerCount; i < this.nodeList.length; i++) {
        this.option.series[3].data.push(this.nodeList[i]);
      }
      this.chart.setOption(this.option);
      this.stepOperationCount++;
    },
    remove() {
      this.nodeList.sort(function(node1, node2) {
        return node1[2] - node2[2];
      })
      this.nodeList = this.nodeList.slice(0, this.ACOSettings.winnerCount);

      this.stepRunningLog += '第 ' + this.activeStepIndex + ' 代优胜：\n';
      for (let j = 0; j < this.nodeList.length; j++) {
        this.stepRunningLog += '(' + this.nodeList[j][0] + ', ' + this.nodeList[j][1] + ')';
        if (j % 2 == 1) {
          this.stepRunningLog += '\n';
        } else {
          this.stepRunningLog += ' ';
        }
      }
      if (this.nodeList.length % 2 != 0) {
        this.stepRunningLog += '\n';
      }

      this.nodeArray.push(this.nodeList);
      this.option.series[1].data.push(this.nodeList[0]);
      this.option.series[2].data = this.nodeList;
      this.option.series[3].data = [];
      this.chart.setOption(this.option);
      this.activeStepIndex++;
      this.stepOperationCount++;
    },
    zoomIn() {
      var truncateIndex = this.activeStepIndex - 1;
      var changeCount = 0;
      var focusNodeList = [];
      while (truncateIndex >= 0) {
        if (this.nodeArray[truncateIndex][0] != this.nodeArray[truncateIndex + 1][0]) {
          changeCount++;
          focusNodeList.push(this.nodeArray[truncateIndex][0]);
        }
        if (changeCount < 10) {
          truncateIndex--;
        } else {
          break;
        }
      }
      this.option.series[1].data = focusNodeList;

      var xMin = Infinity, xMax = -Infinity;
      var yMin = Infinity, yMax = -Infinity;
      var lossMin = Infinity, lossMax = -Infinity;
      var nodes = this.option.series[1].data.concat(this.option.series[2].data).concat(this.option.series[3].data);
      for (let i = 0; i < nodes.length; i++) {
        if (nodes[i][0] < xMin) {
          xMin = nodes[i][0];
        }
        if (nodes[i][0] > xMax) {
          xMax = nodes[i][0];
        }
        if (nodes[i][1] < yMin) {
          yMin = nodes[i][1];
        }
        if (nodes[i][1] > yMax) {
          yMax = nodes[i][1];
        }
        if (nodes[i][2] < lossMin) {
          lossMin = nodes[i][2];
        }
        if (nodes[i][2] > lossMax) {
          lossMax = nodes[i][2];
        }         
      }
      
      this.option.visualMap.min = lossMin;
      this.option.visualMap.max = lossMax;
      var sampleRate = 200;
      this.option.series[0].equation.x = {
        step: (xMax - xMin) / sampleRate,
        min: xMin,
        max: xMax
      }
      this.option.series[0].equation.y = {
        step: (yMax - yMin) / sampleRate,
        min: yMin,
        max: yMax        
      }
      
      this.chart.setOption(this.option);
    },
    zoomOut() {
      this.drawTrace();
    },
    roll() {
      if (this.rolling || this.rollDisabled) {
        return;
      }

      this.rolling = true;
      const { wheelDeg } = this;
      var selectIndex = 0;
      const random = Math.random();
      while (selectIndex < this.wheelSelection.length) {
        if (random <= this.wheelSelection[selectIndex]) {
          break;
        }
        selectIndex++;
      }

      this.wheelDeg = wheelDeg - wheelDeg % 360 + 2 * 360 + (360 - 360 / this.wheelSelection.length * selectIndex);
      this.selectNode = this.nodeList[selectIndex];
      setTimeout(() => {
        this.rolling = false;
        this.stepRunningLog += '轮盘赌结果: ' + selectIndex + ', 选择坐标: (' + this.selectNode[0] + ', ' + this.selectNode[1] + '\n';
      }, 2000);

      var silentNodes = [];
      for (let i = 0; i < this.nodeList.length; i++) {
        if (i != selectIndex) {
          silentNodes.push(this.nodeList[i]);
        }
      }

      this.option.series[2].data = silentNodes;
      this.option.series[3].data = [this.selectNode];
      this.chart.setOption(this.option);

      this.stepOperationCount++;
    },
    outputLog() {
      if (this.mode == 'setting') {
        this.$message.error('请先保存参数');
      } else if(this.mode == 'stepRunning') {
        this.$message.error('请先退出调试');
      } else if (this.simulationLog == '') {
        this.$message.error('当前暂无数据');
      } else {
        this.mode = 'output';
        this.outputMode = 'log';
      }
    },
    outputLoss() {
      if (this.mode == 'setting') {
        this.$message.error('请先保存参数');
      } else if (this.mode == 'stepRunning') {
        this.$message.error('请先退出调试');
      } else if (this.nodeArray == null) {
        this.$message.error('当前暂无数据');
      } else {
        this.mode = 'output';
        this.outputMode = 'loss';
        var averageLoss = [], bestLoss = [];
        for (let i = 0; i < this.nodeArray.length; i++) {
          bestLoss.push([i, this.nodeArray[i][0][2]]);
          var iterationLossSum = 0;
          for (let j = 0; j < this.ACOSettings.winnerCount; j++) {
            iterationLossSum += this.nodeArray[i][j][2];
          }
          averageLoss.push([i, iterationLossSum / this.ACOSettings.winnerCount]);
        }

        this.chart = this.$echarts.init(document.getElementById('loss'));
        this.chart.setOption({
          title: {
            text: '损失函数值变化趋势',
            left: 'center'
          },
          tooltip: {
            trigger: 'axis'
          },
          legend: {
            data: ['最优值', '平均值'],
            top: 25,
          },
          animation: true,
          grid: {
            left: '15%',
            right: '15%',
            show: false
          },
          xAxis: {
            type: 'value',
            min: 'dataMin',
            max: 'dataMax'
          },
          yAxis: {
            scale: true,
            type: 'value',
            boundaryGap: ['5%', '5%']
          },
          series: [
            {
              name: '最优值',
              type: 'line',
              data: bestLoss,
              smooth: true,
              showSymbol: false,
              hoverAnimation: true,
              lineStyle: {
                width: 2
              }
            }, {
              name: '平均值',
              type: 'line',
              data: averageLoss,
              smooth: true,
              showSymbol: false,
              hoverAnimation: true,
              lineStyle: {
                width: 2
              }
            }
          ]
        })
      }
    },
    output2D() {
      if (this.mode == 'setting') {
        this.$message.error('请先保存参数');
      } else if (this.mode == 'stepRunning') {
        this.$message.error('请先退出调试');
      } else if (this.nodeArray == null) {
        this.$message.error('当前暂无数据');
      } else {
        this.mode = 'output';
        this.outputMode = '2D';
        var xData = [];
        for (let i = 0; i <= 100; i++) {
          xData.push(((this.taskSettings.rangeMax - this.taskSettings.rangeMin) * i / 100 + this.taskSettings.rangeMin).toFixed(2));
        }
        var yData = [];
        for (let j = 0; j <= 100; j++) {
          yData.push(((this.taskSettings.rangeMax - this.taskSettings.rangeMin) * j / 100 + this.taskSettings.rangeMin).toFixed(2));
        }

        var heatMapData = [];
        var heatMapMin = Infinity, heatMapMax = -Infinity;
        for (let i = 0; i <= 100; i++) {
          let x = xData[i];
          for (let j = 0; j <= 100; j++) {
            let y = yData[j];
            let value = this.loss(x, y);
            heatMapData.push([i, j, value]);
            if (value < heatMapMin) {
              heatMapMin = value;
            }
            if (value > heatMapMax) {
              heatMapMax = value;
            }
          }
        }

        var champions = [];
        for (let i = 0; i < this.nodeArray.length; i++) {
          let x = (this.nodeArray[i][0][0] - this.taskSettings.rangeMin) / (this.taskSettings.rangeMax - this.taskSettings.rangeMin) * 100;
          let y = (this.nodeArray[i][0][1] - this.taskSettings.rangeMin) / (this.taskSettings.rangeMax - this.taskSettings.rangeMin) * 100;
          champions.push([x, y]);
        }

        this.chart = this.$echarts.init(document.getElementById('2D'));
        this.option = {
          tooltip: {},
          toolbox: {
            show: true,
            feature: {
              dataZoom: {
                show: true
              }
            }
          },
          title: {
            text: '2D迭代过程简图',
            left: 'center'
          },
          grid: {
            left: '15%',
            right: '15%',
            show: false,
            z: 0
          },
          xAxis: {
            type: 'category',
            data: xData
          },
          yAxis: {
            type: 'category',
            data: yData
          },
          visualMap: {
            min: heatMapMin,
            max: heatMapMax,
            calculable: true,
            realtime: false,
            inRange: {
                color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
            },
            seriesIndex: 0
          },
          series: [
            {
              name: this.taskSettings.lossFunction,
              type: 'heatmap',
              data: heatMapData,
              emphasis: {
                  itemStyle: {
                      borderColor: '#333',
                      borderWidth: 1
                  }
              },
              animation: false,
            },
            {
              type: 'line',
              data: champions,
              symbol: 'triangle',
              symbolSize: 10,
              lineStyle: {
                color: 'black',
                width: 2
              }
            }
          ]
        };

        this.chart.setOption(this.option);
      }
    },
    output3D() {
      if (this.mode == 'setting') {
        this.$message.error('请先保存参数');
      } else if (this.mode == 'stepRunning') {
        this.$message.error('请先退出调试');
      } else if (this.nodeArray == null) {
        this.$message.error('当前暂无数据');
      } else {
        this.mode = 'output';
        this.outputMode = '3D';

        var sampleRate = 200;
        var lossMin = Infinity, lossMax = -Infinity;
        for (let i = 0; i <= sampleRate; i++) {
          let x = this.taskSettings.rangeMin + (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate * i;
          for (let j = 0; j <= sampleRate; j++) {
            let y = this.taskSettings.rangeMin + (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate * j;
            let value = this.loss(x, y);
            if (value < lossMin) {
              lossMin = value;
            }
            if (value > lossMax) {
              lossMax = value;
            }
          }
        }

        var champions = [];
        for (let i = 0; i < this.nodeArray.length; i++) {
          let x = this.nodeArray[i][0][0];
          let y = this.nodeArray[i][0][1];
          let value = this.loss(x, y);
          champions.push([x, y, value]);
        }

        this.chart = this.$echarts.init(document.getElementById('3D'));
        this.option = {
          tooltip: {},
          global: {
            left: 'center',
            top: 'center'
          },
          visualMap: {
            show: true,
            dimension: 2,
            min: lossMin,
            max: lossMax,
            inRange: {
                color: ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026']
            },
            seriesIndex: 0
          },
          xAxis3D: {
              type: 'value'
          },
          yAxis3D: {
              type: 'value'
          },
          zAxis3D: {
              type: 'value'
          },
          grid3D: {
              viewControl: {
                projection: 'orthographic'
              },
              boxHeight: 60
          },
          series: [
            {
              type: 'surface',
              wireframe: {
                show: false
              },
              shading: 'color',
              equation: {
                x: {
                  step: (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate,
                  min: this.taskSettings.rangeMin,
                  max: this.taskSettings.rangeMax
                },
                y: {
                  step: (this.taskSettings.rangeMax - this.taskSettings.rangeMin) / sampleRate,
                  min: this.taskSettings.rangeMin,
                  max: this.taskSettings.rangeMax
                },
                z: this.loss
              },
              itemStyle: {
                opacity: 0.8
              }
            },
            {
              type: 'line3D',
              data: champions,
              lineStyle: {
                width: 4,
                color: 'black'
              }
            },
            {
              type: 'scatter3D',
              data: champions,
              symbol: 'triangle',
              symbolSize: 20,
              itemStyle: {
                color: 'black'
              }
            }
          ]
        };
        this.chart.setOption(this.option);
      }
    },
    quit() {
      this.mode = 'ready';
    },
    copyToClipboard(content) {
      if (window.clipboardData) {
        window.clipboardData.setData('text', content);
      } else {
        (function(content) {
          document.oncopy = function(e) {
            e.clipboardData.setData('text', content);
            e.preventDefault();
            document.oncopy = null;
          }
        })(content);
      }
      document.execCommand('Copy');
    },
    downloadChart() {
      if (this.chart == null) {
        this.$message.error('当前无图像可下载，请仿真或调试后重试');
        return;
      }

      var url = this.chart.getConnectedDataURL({
        backgroundColor: '#fff',
        type: 'png'
      });

      var $a = document.createElement('a');
      $a.download = this.outputMode + '.png';
      $a.target = '_blank';
      $a.href = url;

      if (typeof MouseEvent === 'function') {
        var evt = new MouseEvent('click', {
          view: window,
          bubbles: true,
          cancelable: false
        });
        $a.dispatchEvent(evt);
      } else {
        var html = ''

        '<body style="margin: 0;>'
        '![](' + url + ')'
        '</body>';

        var tab = window.open();
        tab.document.write(html);
      }

      this.$message({
        type: 'success',
        message: '图片已下载'
      });
    },
    sphere(x, y) {
      return x * x + y * y;
    },
    easom(x, y) {
      return -Math.cos(x) * Math.cos(y) * Math.exp(-x * x - y * y);
    },
    giewangk(x, y) {
      var a = (x * x + y * y) / 4000;
      var b = Math.cos(x) * Math.cos(y / Math.sqrt(2));
      return -1 / (a - b + 1.1);
    },
    normal() {
      var u = 0.0, v = 0.0, w = 0.0, c = 0.0;
      do {
        u = Math.random() * 2 - 1.0;
        v = Math.random() * 2 - 1.0;
        w = u * u + v * v;
      } while (w == 0.0 || w >= 1.0)
      c = Math.sqrt(-2 * Math.log(w) / w);
      return u * c;
    }
  }
}
</script>

